@import "sass/index";

$limboInputHeight: 40px;

.wrapper {
  width: 100%;
  position: relative;
  overflow: hidden;

  & > div > label {
    line-height: 1.0625rem;
    font-size: 1rem;

    font-family: "Gilroy";
    font-weight: bold;

    color: use(--template-input-text-color);

    opacity: 0.8;
  }
}

.root {
  height: $limboInputHeight;
  box-shadow: none !important;
}

.disabled {
  color: use(--template-input-text-color, 0.6);
}

.coinImg {
  top: $limboInputHeight + 10px;
  margin-top: 2px;
}

.infinityInput {
}

.infinityImg {
  position: static;

  width: 24px;
  height: 24px;
  & > path {
    fill: currentColor;
  }
}

.showInfinityImg {
  transform: translateX(0);
}
